(function () {
  'use strict';

  angular
    .module('lhr70')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController() {
    var vm = this;

    vm.cards = [];

    //= story.getAll();

    var cardsRepo = [{float:'left',items:["lg", "sm", "sq"]}, {float:'right',items:["lg", {type: 'sq', name: "asfd", slug: "asdf"}, "sm"]}, {float:'right' ,items:["sq", "sm"]},{float:'left' ,items:["lg", "sm", "sq"]},{float:'right',items:["lg", "sm", "sq"]}];

    for (var i = 0; i < cardsRepo.length; i++) {
      var cardsSection = cardsRepo[i].items;
      var tempCards = [];

      for (var j = 0; j < cardsSection.length; j++) {
        var card = cardsSection[j];

        var image = 'https://unsplash.it/600?image=' + Math.floor((Math.random() * 100) + 1);

        if (card == "sq") {
          image = "";
        }

        var object = {
          id: i,
          className: card,
          image: image
        };
        tempCards.push(object);
      }

      var cards = {items: tempCards, float: cardsRepo[i].float};

      vm.cards.push(cards);
    }
  }
})();

