(function () {
  'use strict';

  angular
    .module('lhr70')
    .directive('storiesBlock', storiesBlock);

  /** @ngInject */
  function storiesBlock() {
    var directive = {
      restrict: 'EA',
      templateUrl: 'app/components/stories/stories.directive.html',
      scope: {
        cards: '='
      },
      controller: StoriesController,
      controllerAs: 'vm',
      bindToController: true
    };

    /** @ngInject */
    function StoriesController() {
      var vm = this;
      for (var i = 0; i < vm.cards.length; i++) {
        var stories = vm.cards[i].items;
        for (var j = 0; j < stories.length; j++) {
          var story = stories[j];
          if (story.className == 'sq') {
            story.background = "";
          } else {
            story.background = {'background-image': 'url(' + story.image + ')'};
          }
        }
      }
    }

    return directive;
  }
})();



