/**
 * @ngdoc directive
 * @name lhr70:imageCropperDirective
 *
 * @description
 *
 *
 * @restrict A
 * */
angular.module('lhr70')
  .directive('imageCropper', function ($window, $document, $timeout, $media) {
    return {
      restrict: 'A',
      link: function (scope, elem) {

        var debouncer = function (func, timeout) {
          var timeoutID;
          timeout = timeout || 600;
          return function () {
            var scope = this, args = arguments;
            clearTimeout(timeoutID);
            timeoutID = setTimeout(function () {
              func.apply(scope, Array.prototype.slice.call(args));
            }, timeout);
          }
        };

        var crop = function () {
          var jqImage = angular.element(elem);
          var imageCopy = jqImage.find(".story--copy");
          var canvas = angular.element('<canvas></canvas>')[0];
          var ctx = canvas.getContext('2d');
          var img = angular.element('<img></img>')[0];
          img.setAttribute('crossOrigin', 'anonymous');
          img.src = scope.card.image;

          // When the image is loaded, draw it
          img.onload = function () {
            var height = img.height;
            var heightSm = imageCopy.outerHeight();
            var width = img.width;
            var widthSm = imageCopy.outerWidth();

            canvas.height = height;
            canvas.width = width;


            if ($media.query('tablet')) { // is at least table in portrait mode
              var isSmall = jqImage.hasClass('sm');
              var isLeft = jqImage.hasClass('left');
              var isBottom = jqImage.hasClass('bottom');
              imageCopy.addClass('bg-transparent');

              jqImage.removeClass('visible');


              if (isBottom) {
                heightSm = jqImage.height() - heightSm - 20;
                if (!isSmall) {
                  heightSm = heightSm - 50;
                }
              }

              if (isSmall) {
                heightSm = heightSm * 1.5;
                if (isLeft) {
                  widthSm = widthSm * 1.2;
                } else {
                  if ($media.query('screen-sm')) {
                    widthSm = widthSm * 2.5;
                  } else if ($media.query('screen-md')) {
                    widthSm = widthSm * 2;
                  } else {
                    widthSm = widthSm * 1.8;
                  }
                }
              } else {
                if ($media.query('screen-sm')) {
                  widthSm = widthSm * 0.4;
                  heightSm = heightSm * 0.8;
                }  else if ($media.query('screen-md')) {
                  widthSm = widthSm * 0.9;
                  heightSm = heightSm * 1.3;
                } else {
                  widthSm = widthSm * 0.8;
                  heightSm = heightSm * 1;
                }

                heightSm = heightSm * 1.3;
              }


              // Save the state, so we can undo the clipping
              ctx.save();
              ctx.beginPath();
              if (isLeft) {
                if (isBottom) {
                  ctx.moveTo(0, 0);
                  ctx.lineTo(width, 0);
                  ctx.lineTo(width, height);
                  ctx.lineTo(widthSm, height);
                  ctx.lineTo(widthSm, heightSm);
                  ctx.lineTo(0, heightSm);
                } else {
                  ctx.moveTo(0, heightSm);
                  ctx.lineTo(widthSm, heightSm);
                  ctx.lineTo(widthSm, 0);
                  ctx.lineTo(width, 0);
                  ctx.lineTo(width, height);
                  ctx.lineTo(0, height);
                }
              } else {
                if (isBottom) {
                  ctx.moveTo(0, 0);
                  ctx.lineTo(width, 0);
                  ctx.lineTo(width, heightSm);
                  ctx.lineTo(widthSm, heightSm);
                  ctx.lineTo(widthSm, height);
                  ctx.lineTo(0, height);
                } else {
                  ctx.moveTo(width, heightSm);
                  ctx.lineTo(widthSm, heightSm);
                  ctx.lineTo(widthSm, 0);
                  ctx.lineTo(0, 0);
                  ctx.lineTo(0, height);
                  ctx.lineTo(width, height);
                }
              }

              ctx.closePath();
              //ctx.stroke();

              // Clip to the current path
              ctx.clip();
            } else {
              imageCopy.removeClass('bg-transparent');
            }
            ctx.drawImage(img, 0, 0);

            // Undo the clipping
            ctx.restore();

            jqImage.css('backgroundImage',
              "url('" + canvas.toDataURL("image/png") + "')");
            jqImage.addClass('visible');
          };
        };

        $timeout(function () {
          if (scope.$parent.i == 0 && scope.j < 5) {
            angular.element(elem).addClass('croppable');

            crop();
            angular.element($window).bind('resize', debouncer(function () {
              crop();
            }, 50));
          }
        }, 200);


      }
    };
  });

